@charset "utf-8";

// Global styles for the Lanzara Public Site
// These override default settings from the theme we are using, in addition to providing
// various utility classes that are needed for formatting + layout in some places

@import "vendor/bootstrap-carousel.css";
@import "vendor/tailwind.min.css";

/* Components */

/* Typography */
.carousel-container {
  background: rgba(0, 0, 0, 0.4);
}

.team-member {
  max-width: 32rem;
}

.text-white-alpha-800 {
  color: rgba(255, 255, 255, 0.8)
}

.text-white-alpha-600 {
  color: rgba(255, 255, 255, 0.6)
}

.text-white-alpha-400 {
  color: rgba(255, 255, 255, 0.4)
}

.project-header, .landing-splash {
  background: linear-gradient(0.3turn, #0077b6, #00b4d8);

}

.dropdown.closed {
  .dropdown-content {
    display: none;
  }
}

.portrait {
  width: 180px !important;
  min-width: 180px;
  img {
    min-width: 180px;
    width: 180px;
  }
}

.research-highlights .highlight {
  margin: 2rem 4rem;
}

.carousel-video {
  width: 100% !important;
  background-size: cover;
  z-index: -1;
}

#landing-carousel {
  max-width: 800px;
  video {
    max-width: 100%;
  }
  .item {
    img {
      height: 100%;
      object-fit: cover;
    }

    video {
      max-height: 350px;
      object-fit: cover;
    }

    max-height: 350px;
    height: 350px;
    overflow: hidden;
  }
}

body.landing #main {
  padding-left: 0;
  padding-right: 0;
}

// Customize media queries
@media (min-width: 80em) {
  .masthead__inner-wrap {
    max-width: 100%;
  }
  .page__footer footer {
    max-width: 100%;
  }

  #main {
    max-width: 1600px;
  }
}


.page__footer-funding {
  .flex-item {
    max-height: 40px;
    img {
      height: 40px;
    }
  }
}

.page__footer footer {
  display: flex;
  flex-flow: row;

  margin-top: 0.5rem;
  padding-bottom: 0.5rem;

  .page__footer-follow {
    margin-left: 0;
    margin-right: auto;

    li {
      padding: 0;
      margin-bottom: 0;
      line-height: 40px;

      svg {
        margin-right: 4px;
      }
    }
  }

  .page__footer-funding {
    margin-left: auto;
    margin-right: 0;

    .flex-item:not(:last-child) {
      margin-right: 0.5rem;
    }
  }
}

.sidebar.sticky {
  height: unset;
  max-height: calc(100% - 8em);
  overflow-y: scroll;
}

figure.figure {
  display: inline-block;
  margin: 0.5rem;
}

/* reset font animations to exclude KaTeX, this prevents bouncing during resizing */
b, i, strong, em, blockquote, p, q, span, figure, img, h1, h2, header, input, a, tr, td, form button, input[type="submit"],
.btn, #goog-wm-sb, .highlight, .archive__item-teaser {
  &:not(.katex) {
    -webkit-transition: none;
    transition: none;
  }
}

@media (max-width: 500px) {
  html .page__footer footer {
    flex-direction: column;
    flow-flow: column;
  }
}